<template>
  <el-container id="components-dashboard-component">
    <!--Aside Filter-->
    <el-aside class="aside" :class="{ 'aside-active': hasAside }">
      <div class="search-container">
        <el-input
          class="search-input"
          v-model="searchContent"
          placeholder="Search Fabric"
          clearable
        ></el-input>
        <div class="hide-button-container" @click="onClick(false)">
          <p>Hide</p>
          <img
            class="hide-icon"
            src="@/assets/images/icons/png/switch.png"
            alt=""
          />
        </div>
      </div>
      <CollapseItem
        v-for="(option, index) in filterOptions"
        :key="index"
        :title="option.title"
      >
        <template v-if="option.title === 'Color'">
          <el-row class="colors-container" :gutter="5">
            <el-col
              class="color-wrapper"
              v-for="(item, _index) in option.items"
              :key="_index"
              :span="4"
              @click="onColorClick(_index)"
            >
              <div
                class="color-circle"
                :class="{ 'color-active': colorIndex === _index }"
                :style="{ background: item.label }"
              ></div>
            </el-col>
          </el-row>
        </template>
        <template v-else>
          <div
            v-for="(item, _index) in option.items"
            :key="_index"
            class="option"
          >
            <input type="checkbox" v-model="item.value" />
            <div class="name">{{ item.label }}</div>
          </div>
        </template>
      </CollapseItem>
    </el-aside>

    <div v-if="hasOpenButton" class="open-aside-button" @click="onClick(true)">
      <img
        class="arrow-right-icon"
        src="@/assets/images/icons/png/arrow-right.png"
        alt=""
      />
    </div>

    <!--Fabric Cards-->
    <el-main class="main-container">
      <div v-if="tags.length" class="tags-container">
        <FilterTag
          v-for="(tag, index) in tags"
          :key="index"
          :data="tag"
          @close="onTagCloseClick"
        ></FilterTag>
        <p class="button-clear-all-tags" @click="onCloseAllTags">Clear All</p>
      </div>
      <el-row :gutter="30">
        <el-col
          class="col"
          v-for="index in 10"
          :key="index"
          :span="hasAside ? 8 : 6"
        >
          <FabricCard has-buttons></FabricCard>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script lang="ts">
import { defineComponent, ref, watchEffect } from "vue";
import { FilterOption } from "@/typings/asideFilter";

import FilterTag from "@/components/FilterTag.vue";
import FabricCard from "@/components/FabricCard.vue";
import CollapseItem from "@/components/CollapseItem.vue";

import {
  closeAllTags,
  closeTag,
  watchFilterOptions
} from "@/utils/asidtFilter";

const Component = defineComponent({
  name: "Fabrics",
  components: {
    FilterTag,
    FabricCard,
    CollapseItem
  },
  setup() {
    const filterOptions = ref<FilterOption[]>([
      {
        title: "Sort By",
        items: [
          { label: "recommended for you", value: false },
          { label: "popular", value: false }
        ]
      },
      {
        title: "Color",
        items: [
          { label: "red", value: false },
          { label: "red", value: false },
          { label: "red", value: false },
          { label: "red", value: false },
          { label: "red", value: false },
          { label: "red", value: false },
          { label: "red", value: false },
          { label: "red", value: false },
          { label: "red", value: false },
          { label: "red", value: false },
          { label: "red", value: false },
          { label: "red", value: false }
        ]
      },
      {
        title: "Material",
        items: [
          { label: "111111", value: false },
          { label: "222222", value: false }
        ]
      },
      {
        title: "Benefit",
        items: [
          { label: "33333", value: false },
          { label: "444444", value: false }
        ]
      },
      {
        title: "Construction",
        items: [
          { label: "555555", value: false },
          { label: "666666", value: false }
        ]
      },
      {
        title: "Weight",
        items: [
          { label: "777777", value: false },
          { label: "888888", value: false }
        ]
      },
      {
        title: "Height",
        items: [
          { label: "9", value: false },
          { label: "10", value: false }
        ]
      }
    ]);
    const tags = ref<string[]>([]);
    const hasAside = ref(true);
    const hasOpenButton = ref(false);
    const colorIndex = ref(-1);
    const searchContent = ref("");

    watchEffect(() => {
      tags.value = watchFilterOptions(filterOptions.value);
    });

    const onClick = (value: boolean) => {
      hasAside.value = value;

      if (!value) {
        setTimeout(() => {
          hasOpenButton.value = true;
        }, 300);
      } else {
        hasOpenButton.value = false;
      }
    };

    const onColorClick = (index: number) => {
      colorIndex.value = index;
    };

    const onTagCloseClick = (tag: string) => {
      filterOptions.value = closeTag(filterOptions.value, tag);
    };

    const onCloseAllTags = () => {
      filterOptions.value = closeAllTags(filterOptions.value);
    };

    return {
      tags,
      filterOptions,
      hasAside,
      hasOpenButton,
      searchContent,
      colorIndex,

      onClick,
      onColorClick,
      onTagCloseClick,
      onCloseAllTags
    };
  }
});

export default Component;
</script>

