
import { defineComponent, toRefs } from "vue";
import { useRouter } from "vue-router";

const Component = defineComponent({
  name: "FabricCard",
  props: {
    hasButtons: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const router = useRouter();
    const { hasButtons } = toRefs(props);

    const onImageClick = () => {
      router.push({ name: "FabricDetail", params: { id: "id" } });
    };

    const onFavoriteClick = () => {
      if (!hasButtons) return;
      alert("favorite");
    };
    const onAddClick = () => {
      if (!hasButtons) return;
      alert("add");
    };

    return {
      onImageClick,
      onFavoriteClick,
      onAddClick
    };
  }
});

export default Component;
