<template>
  <div id="fabric-card-component">
    <div class="cover-image" @click="onImageClick"></div>
    <div class="content-container">
      <div
        class="content-wrapper"
        :style="{ width: hasButtons ? '70%' : 'auto' }"
      >
        <h3 class="title">Woven - 84929302</h3>
        <p class="description">content:65%,cotton:35%</p>
      </div>
      <div v-if="hasButtons" class="buttons-group">
        <div class="button-wrapper" @click="onFavoriteClick">
          <img class="icon" src="@/assets/images/icons/png/heart.png" alt="" />
          <p class="icon-name">Favorite</p>
        </div>
        <div class="button-wrapper" @click="onAddClick">
          <img class="icon" src="@/assets/images/icons/png/heart.png" alt="" />
          <p class="icon-name">Add</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs } from "vue";
import { useRouter } from "vue-router";

const Component = defineComponent({
  name: "FabricCard",
  props: {
    hasButtons: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const router = useRouter();
    const { hasButtons } = toRefs(props);

    const onImageClick = () => {
      router.push({ name: "FabricDetail", params: { id: "id" } });
    };

    const onFavoriteClick = () => {
      if (!hasButtons) return;
      alert("favorite");
    };
    const onAddClick = () => {
      if (!hasButtons) return;
      alert("add");
    };

    return {
      onImageClick,
      onFavoriteClick,
      onAddClick
    };
  }
});

export default Component;
</script>

